import React from 'react';
import { styled } from '@mui/system';
import {
  Container,
  Typography,
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  tableCellClasses,
  Grid
} from '@mui/material';
import { makeStyles } from '@material-ui/core';

import { AdministrationCard } from '../../components';

import { theme } from '../../theme';

const useStyles = makeStyles(()=>({
  outerСontainer: {
    background: theme.palette.background.paper,
    padding: '2rem 0 4rem',
  },
  sectionTitle: {
    marginBottom: '2rem',
    fontSize: '2.2rem',
    fontWeight: '800',
    color: theme.palette.text.secondary,
    '&:after': {
      content: '""',
      display: 'block',
      height: 40,
      width: '55%',
      background: 'rgb(211,115,38, 0.3)',
      margin: '-25px 0 0 -25px'
    }
  },
  buttonSection: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  button: {
    display: "block",
    width: '90%',
    marginBottom: "1rem",
    fontSize: '0.825rem'
  },
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.secondary.main,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(even)': {
    backgroundColor: theme.palette.action.hover,
  },
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

const createDataStructure = (name, position, mail, number) => ({
  name,
  position,
  mail,
  number,
});

export const Contacts = ({ contacts, administration }) => {
  const classes = useStyles();
  const rows = contacts.map(({name, position, mail, number}) => createDataStructure(name, position, mail, number));

  return (
    <Container maxWidth={false} className={classes.outerСontainer}>
      <Container maxWidth='xl'>
        <Typography className={classes.sectionTitle}>
          Администрация
        </Typography>
        <Grid container spacing={3}>
          <Grid container item xs={12} md={12} xl={12} justifyContent="center" spacing={3}>
            {
              administration.map((person, ind) =>
                (
                  <Grid item xs={12} md={6} xl={4} key={ind}>
                    <AdministrationCard person={person} key={person.id} />
                  </Grid>
                ))
            }
          </Grid>
        </Grid>
        <Typography className={classes.sectionTitle} sx={{ marginTop: '2rem' }} >
          Контакты
        </Typography>
        <Table >
          <TableHead>
            <TableRow>
              <StyledTableCell>ФИО</StyledTableCell>
              <StyledTableCell>Должность</StyledTableCell>
              <StyledTableCell>Номер телефона</StyledTableCell>
              <StyledTableCell>Почта</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <StyledTableRow key={row.name}>
                <StyledTableCell component="th" scope="row">
                  {row.name}
                </StyledTableCell>
                <StyledTableCell>{row.position}</StyledTableCell>
                <StyledTableCell>{row.number}</StyledTableCell>
                <StyledTableCell>{row.mail}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </Container>
    </Container>
  )
}
