import React from 'react';
import { Card, ListItemText, CardMedia, Typography, CardContent } from '@mui/material';

import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(()=>({
  card: {
    padding: '1rem 1rem 2rem',
  },
  cardContentHeader: {
    display: 'flex',
  },
  cardContent: {
    height: '13rem'
  },
  textContent: {
    padding: '0 2rem',
    display: 'flex',
    flexDirection: 'column',
  },
  cardTitle: {
    fontSize: '1.1rem',
    fontWeight: 500,
  },
  name: {
    paddingBottom: '1rem',
    fontSize: '1.1rem',
    fontWeight: 500,
  },
}));

export const AdministrationCard = ({ person }) => {
    const classes = useStyles();

    return (
        <Card className={classes.card} >
            <CardContent className={classes.cardContentHeader}>
                <CardMedia
                    component="img"
                    image={person.image.url}
                    alt={person.name}
                    sx={{ width: '150px', height: '150px' }}
                />
                <ListItemText
                    className={classes.textContent}
                    primary={<React.Fragment>
                        <Typography className={classes.cardTitle}>
                            {person.position}
                        </Typography>
                    </React.Fragment>}
                    secondary={
                        <div>
                            <Typography>
                                {person.telephone}
                            </Typography>
                        </div>
                    }
                />
            </CardContent>
            <CardContent className={classes.cardContent}>
                <Typography className={classes.name}>
                    {person.name}
                </Typography>
                <Typography>
                    {person.statement}
                </Typography>
            </CardContent>
            </Card>
        );
    }