import React from 'react';
import { Container, Typography, Grid, Button } from '@mui/material';
import { makeStyles } from '@material-ui/core';

import { ServicesCard } from '../../components/ServicesCard';

import { theme } from '../../theme';

const useStyles = makeStyles(()=>({
  outerСontainer: {
    background: theme.palette.background.paper,
    padding: '2rem 0 4rem',
  },
  sectionTitle: {
    marginBottom: '2rem',
    fontSize: '2.2rem',
    fontWeight: '800',
    color: theme.palette.text.secondary,
    '&:after': {
      content: '""',
      display: 'block',
      height: 40,
      width: '55%',
      background: 'rgb(211,115,38, 0.3)',
      margin: '-25px 0 0 -25px'
    }
  },
  buttonSection: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  button: {
    display: "block",
    width: '90%',
    marginBottom: "1rem",
    fontSize: '0.825rem'
  },
}));

export const Services = ({ services, licenses, certificates }) => {
  const classes = useStyles();

  return (
    <Container maxWidth={false} className={classes.outerСontainer}>
      <Container maxWidth='xl'>
        <Typography className={classes.sectionTitle}>
          Наши Услуги
        </Typography>
        <Grid container spacing={3}>
          <Grid container item xs={12} md={8} xl={9} justifyContent="center" spacing={3}>
            {
              services.map((service, ind) =>
                (
                  <Grid item xs={12} md={12} xl={6} key={ind}>
                    <ServicesCard service={service} />
                  </Grid>
                ))
            }
          </Grid>
          <Grid item xs={12} md={4} xl={3} className={classes.buttonSection} >
              <Typography variant='h5' sx={{ mb: '1rem' }}>
                Сертификаты
              </Typography>
              {
                certificates.map((certificate) => 
                  <Button size="small" variant='outlined' color="secondary" key={certificate.id} className={classes.button} onClick={() => window.open(certificate.image.url)}>{certificate.title}</Button>
                )
              }
              <Typography variant='h5' sx={{ m: '2rem 0 1rem' }}>
                Лицензии
              </Typography>
              {
                licenses.map((license) => 
                  <Button size="small" variant='outlined' color="secondary" key={license.id} className={classes.button} onClick={() => window.open(license.image.url)}>{license.title}</Button>
                )
              }
          </Grid>
        </Grid>
      </Container>
    </Container>
  )
}
