import React from 'react';
import { Container, Typography, Grid, ImageList, ImageListItem, Button } from '@mui/material';
import { makeStyles } from '@material-ui/core';
import { useMediaQuery } from '@mui/material';

import Image1 from '../../static/c1.jpg'
import Image2 from '../../static/img2.jpg'
import Image3 from '../../static/img3.jpg'
import Image4 from '../../static/img4.jpg'
import Image5 from '../../static/img5.jpg'
import Image6 from '../../static/img6.jpg'
import Image7 from '../../static/img7.jpg'
import Image8 from '../../static/img8.jpg'
import Image9 from '../../static/img9.jpg'
import { theme } from '../../theme';

const useStyles = makeStyles(()=>({
  outerСontainer: {
    background: theme.palette.background.paper,
    padding: '2rem 0 5.1rem',
  },
  sectionTitle: {
    marginBottom: '2rem',
    fontSize: '2.2rem',
    fontWeight: '800',
    color: theme.palette.text.secondary,
    '&:after': {
        content: '""',
        display: 'block',
        height: 40,
        width: '55%',
        background: 'rgb(211,115,38, 0.3)',
        margin: '-25px 0 0 -25px'
      }
  },
  subtitle: {
    fontSize: '1.8rem',
    fontWeight: '500',
    color: '#E3AD80',
    
  },
  text: {
    paddingBottom: '1rem',
  },
  button: {
    width: '30%',
    marginTop: '2rem',
    padding: '1rem 0',
    fontSize: '1rem',
  },
}));

export const Home = () => {
    const classes = useStyles();
    const isTablet = useMediaQuery(theme.breakpoints.down('lg'));

    const photos = [
        {
            img: Image1,
            title: 1,
        },
        {
            img: Image2,
            title: 2,
        },
        {
            img: Image3,
            title: 3,
        },
        {
            img: Image4,
            title: 4,
        },
        {
            img: Image5,
            title: 5,
        },
        {
            img: Image6,
            title: 6,
        },
        {
            img: Image7,
            title: 7,
        },
        {
            img: Image8,
            title: 8,
        },
        {
            img: Image9,
            title: 9,
        },
    ];

    return (
        <Container maxWidth={false} className={classes.outerСontainer}>
            <Container maxWidth='xl'>
                <Grid container spacing={3}>
                    <Grid item xs={12} md={12} lg={7}>
                        <Typography className={classes.subtitle}>
                            Открытое Акционерное Общество
                        </Typography>
                        <Typography className={classes.sectionTitle}>
                            Институт Гомельоблстройпроект
                        </Typography>
                        <Typography className={classes.text}>
                            ОАО «Институт Гомельоблстройпроект» с момента  его создания в 1960 году и до настоящего времени является областной проектной организацией по проектированию на селе и в районных центрах.
                        </Typography>
                        <Typography className={classes.text}>
                            ОАО "Институт Гомельоблстройпроект получил официальное свидетельство на проведение оценки воздействия на окружающую среду по компонентам природной среды: вода, атмосферный воздух,  недра, растительный мир, земли (включая почвы). ОАО «Институт Гомельоблстройпроект» является членом СРО «Брянское региональное Объединение Проектировщиков» и СРО «Ассоциация Инженерные изыскания в строительстве». 
                        </Typography>
                        <Button size="large" variant='contained' color="secondary" className={classes.button} onClick={() => window.location = "mailto:org_gosp@gosp.by"}>Связаться с нами</Button>
                    </Grid>
                    <Grid item xs={12} md={12} lg={5}>
                        <ImageList sx={{ width: '100%' }} cols={isTablet ? 5 : 3} rowHeight={164}>
                            {photos.map((item) => (
                                <ImageListItem key={item.img}>
                                    <img
                                        src={item.img}
                                        srcSet={item.img}
                                        alt={item.title}
                                        loading="lazy"
                                    />
                                </ImageListItem>
                            ))}
                        </ImageList>
                    </Grid>
                </Grid>
            </Container>
        </Container>
    )
}
