import React from 'react';
import { Container, Grid, Typography } from '@mui/material';
import { makeStyles } from '@material-ui/core';
import { useMediaQuery } from '@mui/material';

import { DrawerComponent } from './Drawer';
import logo from '../../static/logo2.png'

import { theme } from '../../theme';

const useStyles = makeStyles(()=>({
  headerContentContainer: {
    height: '115px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  alignment: {
    alignItems: 'center',
  }
}));

export const Header = ({ children }) => {
  const classes = useStyles();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  return (
    <Container maxWidth={false} className={classes.outerСontainer}>
      <Container maxWidth='xl'>
        <Grid container className={classes.headerContentContainer}>
          <Grid item container xs={10} md={5} lg={7} sx={{ alignItems: 'center' }}>
            <Grid item xs={4} md={4} lg={2}>
              <img
                  src={logo}
                  width='100'
                  alt='logo'
                  loading="lazy"
              />
            </Grid>
            <Grid item xs={8} md={8} lg={10} className={classes.alignment}>
              {
                isMobile ? <Typography>ГОСП</Typography> : <Typography>ОАО "Институт Гомельоблстройпроект"</Typography>
              }
            </Grid>
          </Grid>
          <Grid item xs={2} md={7} lg={5}>
            {isMobile ? (
              <DrawerComponent/>
              ) : (
                children
              )
            }
          </Grid>
        </Grid>
      </Container>
    </Container>
  )
}
