import React, { useState } from "react";
import { Card, ListItemText, Typography, CardMedia, Button, Modal, Box, List, ListItem } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';

import { makeStyles } from '@material-ui/core';
import { theme } from "../../theme";

const useStyles = makeStyles(()=>({
  card: {
    display: 'flex',
    height: '150px',
    padding: '1rem 1rem 2rem',
  },
  textContent: {
    padding: '0 2rem',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  cardTitle: {
    fontSize: '1.1rem',
    fontWeight: 500,
  },
  button: {
    fontSize: '0.825rem',
  },
  modal: {
    position: 'absolute',
    maxHeight: '50vh',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '70%',
    background: theme.palette.background.paper,
    border: '1px solid #333',
    borderRadius: 4,
    boxShadow: 24,
    padding: '2rem',
  }
}));

export const ServicesCard = ({ service }) => {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);


  return (
    <Card className={classes.card} >
      <CardMedia
        component="img"
        image={service.image.url}
        alt={service.title}
        sx={{ width: '30%' }}
      />
      <ListItemText
        className={classes.textContent}
        primary={<React.Fragment>
          <Typography className={classes.cardTitle}>
            {service.title}
          </Typography>
        </React.Fragment>}
        secondary={
            <React.Fragment>
              <Button variant="outlined" color="secondary" className={classes.button} onClick={handleOpen} endIcon={<SendIcon />}>
                Узнать больше
              </Button>
            </React.Fragment>
          }
        />
        <Modal
          open={open}
          onClose={handleClose}
        >
          <Box className={classes.modal}>
            <Typography
              variant="h5" gutterBottom
            >
              {service.title}
            </Typography>
            <Typography sx={{ fontSize: 18 }}>
              {service.description}
            </Typography>
            <List sx={{ width: '100%' }}>
              {service.servicesList.map((value, ind) => (
                <ListItem
                  disableGutters
                  key={value}
                  sx={{ padding: 0 }}
                >
                  <ListItemText primary={`${ind + 1}. ${value}`} />
                </ListItem>
              ))}
            </List>
          </Box>
        </Modal>
      </Card>
  );
}
